.movieRow {
  margin-bottom: 30px;
}

.movieRow h2 {
  margin: 0px 0px 0px 30px;
}

.movieRow--listarea{
  padding-left: 30px ;
  overflow-x: hidden;
}

.movieRow--list{
  transition:  all ease 0.5s;
}
.movieRow--item{
  display: inline-block;
  width: 150px;
  cursor: pointer;
}
.movieRow--item img {
  width: 100%;
  transform: scale(0.9);
  transition: all ease 0.25s;
}

.movieRow--item img:hover{
  transform: scale(1);
}

.movieRow--left,
.movieRow--right{
  margin-top: 32px;
  position: absolute;
  width: 40px;
  height: 225px;
  background-color:rgba(0, 0, 0, 0.247);
  z-index:99;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  cursor: pointer;
  opacity: 0;
  transition: all ease 0.4s;
}

.movieRow:hover .movieRow--left ,
.movieRow:hover .movieRow--right{
  opacity: 1;

}
    
.movieRow--left img,
.movieRow--right img{
  width: 45px;
  padding: 5px;
}
.movieRow--left{
  left: 0;
}

.movieRow--right{
  right: 0;
}



@media screen and (max-width: 760px){
  .movieRow--left,
  .movieRow--right{
    opacity: 1;
  }
}


header {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  background: transparent;
  transition: all ease 0.5s;
}

header.black{
  background-color: #000000;
}
.header--logo{
  height: 30px;
}

.header--logo img {
  height: 100%;

}

.header--user{
  height: 40px;

}

.header--user img{
  height: 100%;
  background-color: white;
  border-radius: 3px;
}

.featured{
  height: 100vh;
  
}

.featured--vertical{
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal{
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 30%, transparent 70%) ;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
  padding-bottom: 150px;
  padding-top: 70px;
}

.featured--name{
  font-size:60px;
  font-weight: bold;

}

.featured--info{
  font-size: 18;
  font-weight: bold;
  margin-top:15px;
}


.featured--points,
.featured--year,
.featured--seasons {
  display: inline-block;
  margin-right: 15px;
}

.featured--points{
  color: #46d369 ;
}

.featured--description {
  margin-top: 15px;
  font-size: 20px;
  color: #999;
  max-width: 40%;
}

.featured--buttons{
  margin-top:15px;
}

.featured--watchbutton,
.featured--mylistbutton{
  display: inline-block;
  font-size: 20px;
  font-weight: bold;
  padding: 12px 25px;
  border-radius: 5px;
  text-decoration: none;
  margin-right: 10px;
  opacity: 1;
  transition: all ease 0.2;
}

.featured--watchbutton:hover,
.featured--mylistbutton:hover{
  opacity: 0.7;

}
.featured--watchbutton{
  background-color: #fff;
  color: #000;
}

.featured--mylistbutton{
  background-color: #333;
  color: #fff;
}

.featured--genres{
  margin-top: 15px;
  font-size: 18px;
  color: #999;
}



@media screen and (max-width: 760px){
  .featured{
    height: 90vh;
  }

  .featured--name{
    font-size: 40px;
  }

  .featured--info,
  .featured--watchbutton,
  .featured--mylistbutton
  {
    font-size: 16px;
  }

  .featured--description{
    font-size: 14px;
    max-width: 100%;
    margin-right: 30px;
  }


  .featured--genres{
    font-size: 14px;
  }

}

.info--group{
